.App {
  text-align: center;
  padding-left: 10px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly; /* Adjust as needed */
}

.goods-card {
  border: 1px solid #ddd;
  padding: 16px;
  margin: 10px;
  max-width: 200px;
  text-align: center;
  flex: 0 0 calc(50% - 20px); /* 50% width with margin */
  box-sizing: border-box;
}

.goods-card img {
  max-width: 100%;
  height: auto;
}